import DataService from '../../services/DataService';
let buttons =
    [

        {
            title: 'Back to list',
            iconClass: 'fa fa-angle-double-left pr-2',
            callbackfunction: 'Backtolist',
            additionalClass: '',
            isdisabled: false,
            isvisible: true,
            checkPrivilege: false,

        },

    ];
let leftsearchSchema = [
    {
        fieldName: "User",
        fieldType: "ddl-check",
        fieldIcon: "fa fa-user",
        paramName: "userIds",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },
    {
        fieldName: "Date",
        fieldType: "date-range",
        fieldIcon: "fa-calendar",
        fieldSearchConditionName: ['orders.order_date|orders.order_date'],
        isSearch: false,
        datePickerConfig: {
            useCurrent: false,
            showClear: true,
            showClose: true,
            allowInputToggle: true,
            minDate: false,
            maxDate: false,
            widgetPositioning: {
                horizontal: 'right',
                vertical: 'bottom'
            }
        },
    },
    {
        fieldName: "Category",
        fieldType: "ddl-check",
        fieldIcon: "fa fa-suitcase",
        paramName: "categoryIds",
        fieldSearchConditionName: [''],
        isSearch: false,
        value: "",
        listOptions: []
    },

];



function bindleftCommonSearchdropdown(instance, modulename, filtername) {
    var vm = instance;
    if (filtername == "USERS") {
        var params = `id=&modulename=${modulename}&type=${filtername}&search=&refId=`;
        DataService.GetDDLData(params)
            .then(function (response) {
                let obj1 = {}
                response.data.DATA.forEach(item => {
                    obj1 = {
                        name: item.name,
                        value: (item.value)
                    }
                    vm.leftSearchFields[0].listOptions.push(obj1);
                });
            });
    }
    if (filtername == "Category") {
        var url=`pageSize=500&sortBy=category_name&sortExp=ASC`
        DataService.GetCategoryListing(url)
            .then(function (response) {
                JSON.parse(response.data).data.forEach(item => {
                    let obj1 = {
                        name: item.category_name,
                        value: `${item.category_id}`
                    }
                    vm.leftSearchFields[2].listOptions.push(obj1);
                });
            });
    }


}

export default {
    buttons, leftsearchSchema, bindleftCommonSearchdropdown

};